                        <div class="content">{{ itemData.name }}</div>
                    </Col>
                    <Col span="6">
                        <div class="name">审核状态：</div>
                        <div class="content">{{ itemData.examineStatus }}</div>
                    </Col>
                    <Col span="6">
                        <div class="name">启用状态：</div>
                        <div class="content">{{ itemData.useStatus }}</div>
                    </Col>
                </Row>
            </div>
            <div class="normal-info-table">
                <div class="table">
                    <!-- <div class="cell">
                        <div class="cell-name">语言编码：</div>
                        <div class="cell-content"></div>
                    </div> -->
                    <div class="cell">
                        <div class="cell-name">数据最大长度：</div>
                        <div class="cell-content">{{ itemData.maxLength }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">数据项分类：</div>
                        <div class="cell-content">{{ itemData.itemType }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">控件类型：</div>
                        <div class="cell-content">{{ itemData.controlType }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">数据来源：</div>
                        <div class="cell-content">{{ itemData.source }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">数据来源值：</div>
                        <div class="cell-content">{{ itemData.sourceValue }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">数据类型：</div>
                        <div class="cell-content">{{ itemData.dataType }}</div>
                    </div>
                    <!-- <div class="cell">
                        <div class="cell-name">数据类型：</div>
                        <div class="cell-content">{{ itemData.dataType }}</div>
                    </div> -->
                    <!-- <div class="cell">
                        <div class="cell-name">控件值来源地址：</div>
                        <div class="cell-content"></div>
                    </div> -->
                    <div class="cell">
                        <div class="cell-name">数据精度：</div>
                        <div class="cell-content">{{ itemData.accuracy }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">创建人：</div>
                        <div class="cell-content">{{ itemData.createBy }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">创建时间：</div>
                        <div class="cell-content">{{ timestampFormat(itemData.createDatetime) }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">审核人：</div>
                        <div class="cell-content">{{ itemData.examineBy }}</div>
                    </div>
                    <div class="cell">
                        <div class="cell-name">审核时间：</div>
                        <div class="cell-content">{{ timestampFormat(itemData.examineDatetime) }}</div>
                    </div>
                </div>
                <div class="item">
                    <div class="item-name">备注规则：</div>
                    <div class="item-content">{{ itemData.note }}</div>
                </div>
            </div>
        </div>
        <!-- <div class="public-modify-record">
            <h3 class="table-title">修改记录</h3>
            <div class="collapse">
                <Collapse v-model="value" accordion>
                    <Panel name="1">
                        <Row>
                            <Col span="4">
                                时间：2020/06/16
                            </Col>
                            <Col span="6">
                                修改人：刘笑笑
                            </Col>
                            <Col span="14">修改内容</Col>
                        </Row>
                        <div slot="content">
                            <div class="content">
                                <ul class="record-list">
                                    <li>
                                        <div class="modify-name">修改数据类型：</div>
                                        <div class="before-content"><span class="icon">前</span>字符串</div>
                                        <div class="after-content"><span class="icon">后</span>字符串</div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </Panel>
                </Collapse>
            </div>
        </div> -->
    </div>
</template>

<script>
import { apiTemplateDataQueryDetail } from '@/api/dataItemManage'
import { timestampFormat } from '@/libs/utils'
export default {
    name: 'item-details',
    props: {
        name: ''
    },
    data () {
        return {
            value: '',
            itemData: {}
        }
    },
    created () {
        this.getData()
    },
    methods: {
        // 格式化时间
        timestampFormat (timeStamp) {
            return timestampFormat(timeStamp)
        },
        getData () {
            apiTemplateDataQueryDetail({ name: this.name }).then(({ data }) => {
                this.itemData = data
            })
        }
    }
}
</script>
